.banner-and-header.stuck {
  z-index: 50;
  background-color: #fff;
  width: 100%;
  position: fixed;
  top: 0;
  box-shadow: 0 17px 53px -12px #000000bf;
}

.button {
  position: relative;
  overflow: hidden;
}

.button span {
  z-index: 20;
}

.button:after {
  content: "";
  opacity: .2;
  z-index: 30;
  background: #fff;
  width: 50px;
  height: 155px;
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  animation: 1.5s infinite flash;
  position: absolute;
  top: -50px;
  left: -75px;
  transform: rotate(35deg);
}

.button:hover:after {
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  left: 120%;
}

@keyframes flash {
  0% {
    content: "";
    opacity: .2;
    z-index: 30;
    background: #fff;
    width: 50px;
    height: 155px;
    transition: all .55s cubic-bezier(.19, 1, .22, 1);
    position: absolute;
    top: -50px;
    left: -75px;
    transform: rotate(35deg);
  }

  100% {
    transition: all .55s cubic-bezier(.19, 1, .22, 1);
    left: 120%;
  }
}

.banner {
  background: linear-gradient(315deg, #65005e 3%, #3c84ce 38%, #30eee2 68%, #ff1919 98%) 0 0 / 400% 400% fixed;
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  animation: 15s infinite gradient;
  overflow: auto;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.wave {
  opacity: .8;
  z-index: -1;
  background: #ffffff40;
  border-radius: 1000% 1000% 0 0;
  width: 200%;
  height: 12em;
  animation: 10s linear -3s infinite wave;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  opacity: .8;
  animation: 18s linear infinite reverse wave;
  bottom: -1.25em;
}

.wave:nth-of-type(3) {
  opacity: .9;
  animation: 20s -1s infinite reverse wave;
  bottom: -2.5em;
}

@keyframes wave {
  2% {
    transform: translateX(1px);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1px);
  }
}

@media (width <= 1079px) {
  .body-wrapper {
    display: none;
  }

  .dev-notice {
    display: block;
  }
}

@media (width >= 1080px) {
  .dev-notice {
    display: none;
  }
}

